<template>
  <v-card style="height: 100%">
    <v-layout style="height: 100%">
      <v-navigation-drawer permanent expand-on-hover>
        <v-list nav style="background-color: rgb(241, 241, 232);">
          <v-list-item class="px-2">
            <v-list-item-icon>
              <v-icon>mdi-account</v-icon>
            </v-list-item-icon>
            <v-list-item-title class="text-h6">
              {{ this.user }}
            </v-list-item-title>
            <v-list-item-avatar> </v-list-item-avatar>
          </v-list-item>
        </v-list>

        <v-divider></v-divider>

        <v-list rounded nav dense>
          <v-list-item link @click="route('/process')">
            <v-list-item-icon>
              <v-icon> mdi-application-brackets-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title>
              进程管理
            </v-list-item-title>
          </v-list-item>
          <v-list-item link @click="route('/user')" v-if="checkPer(0)">
            <v-list-item-icon>
              <v-icon>mdi-account-multiple</v-icon>
            </v-list-item-icon>
            <v-list-item-title>用户管理</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="route('/log')" v-if="checkPer(1)">
            <v-list-item-icon>
              <v-icon>mdi-information-slab-circle</v-icon>
            </v-list-item-icon>
            <v-list-item-title>日志中心</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="route('/push')" v-if="checkPer(1)">
            <v-list-item-icon>
              <v-icon>mdi-message-fast</v-icon>
            </v-list-item-icon>
            <v-list-item-title>推送配置</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="route('/config')">
            <v-list-item-icon>
              <v-icon>mdi-cog</v-icon>
            </v-list-item-icon>
            <v-list-item-title>系统设置</v-list-item-title>
          </v-list-item>
        </v-list>
        <v-btn
          class="mx-2"
          fab
          small
          @click="logout"
          style="position: fixed; bottom: 1%;"
        >
          <v-icon dark>
            mdi-logout
          </v-icon>
        </v-btn>
      </v-navigation-drawer>
      <v-main style="width: 0px">
        <router-view></router-view>
      </v-main>
    </v-layout>
  </v-card>
</template>
<script>
export default {
  data() {
    return {
      user: "",
    };
  },
  created() {
    const user = localStorage.getItem("user");
    console.log(user);
    this.user = user;
  },
  methods: {
    route(path) {
      this.$router.push(path);
    },
    logout() {
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      this.$router.push("/login");
    },
    print() {},
  },
};
</script>
<style></style>
