import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import Index from "../views/Index.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/",
    name: "Index",
    component: Index,
    redirect: '/process',
    children: [
      {
        path: "/process",
        name: "Process",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/Process.vue"),
      },
      {
        path: "/user",
        name: "User",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/User.vue"),
      },
      {
        path: "/log",
        name: "Log",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/Log.vue"),
      },
      {
        path: "/push",
        name: "Push",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/Push.vue"),
      },
      {
        path: "/config",
        name: "Config",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/Config.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
