<template>
  <v-container style="margin: 150px;max-width: 80%;">
    <v-row align="center" justify="center">
      <v-col cols="12" sm="10" style="margin: auto;">
        <v-card class="elevation-6 mt-10">
          <v-window v-model="step">
            <v-window-item :value="1">
              <v-row>
                <v-col cols="12" md="6">
                  <v-card-text class="mt-12">
                    <v-row align="center" justify="center">
                      <v-col cols="12" sm="8" style="height: 400px;">
                        <v-form>
                          <v-text-field
                            label="Email"
                            outlined
                            dense
                            v-model="loginForm.account"
                            color="blue"
                            autocomplete="false"
                            class="mt-16"
                            required
                          />
                          <v-text-field
                            label="Password"
                            outlined
                            v-model="loginForm.password"
                            dense
                            color="blue"
                            autocomplete="false"
                            type="password"
                            required
                          />
                        </v-form>
                        <v-btn
                          color="blue"
                          dark
                          block
                          tile
                          @click="handleLogin"
                          style="margin-top: 10%;"
                          >登入</v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-col>
                <v-col cols="12" md="6" class="blue rounded-bl-xl">
                  <div style="  text-align: center; padding: 180px 0;">
                    <v-card-text class="white--text">
                      <h1 class="text-center ">Welcome</h1>
                      <h1 class="text-center" style="margin-top: 20px;">
                        欢迎
                      </h1>
                    </v-card-text>
                  </div>
                </v-col>
              </v-row>
            </v-window-item>
          </v-window>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { login } from "../api/user";

export default {
  data: () => ({
    step: 1,
    loginForm: {
      account: "",
      password: "",
    },
  }),
  methods: {
    handleLogin() {
      login(this.loginForm).then((response) => {
        if (response.code === 0) {
          //dispatch采用Promise链式调用
          localStorage.setItem("token", response.token);
          localStorage.setItem("user", this.loginForm.account);
          localStorage.setItem("role", response.role);
          this.$message.success(`登入成功`);
          this.$router.push("/");
        }
      });
    },
  },
  props: {
    source: String,
  },
};
</script>
<style scoped>
.v-application .rounded-bl-xl {
  border-bottom-left-radius: 300px !important;
}
.v-application .rounded-br-xl {
  border-bottom-right-radius: 300px !important;
}
</style>
