import request from "@/utils/request";

// 登录方法
export function login(data) {
  return request(
    "POST",
    "/user/login",
    {
      account: data.account,
      password: data.password,
    },
    null
  );
}

export function createUser(data) {
  return request("POST", "/user", data, null);
}

export function deleteUser(account) {
  return request("DELETE", "/user", null, { account: account });
}

export function changePassword(data) {
  return request("PUT", "/user/password", data, null);
}

export function registerAdmin(password) {
  return request("GET", "/user/register/admin", null, { password: password });
}

export function getUserList() {
  return request("GET", "/user", null, null);
}

export function getPermission(account,pid) {
  return request("GET", "/permission/list", null, {account:account,pid:pid});
}

export function editPermission(data) {
  return request("PUT", "/permission", data, null);
}
