import Axios from "axios";
import Message from "../message";
import router from "../router/index";

const instance = Axios.create({
  headers: {
    "Content-Type": "application/json",
  },
  timeout: 10000,
});

instance.interceptors.request.use(
  (config) => {
    if (localStorage.getItem("token")) {
      config.headers = {
        token: localStorage.getItem("token"),
        ...config.headers,
      };
    }
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);

instance.interceptors.response.use(
  (res) => {
    const code = res.data.code || 0;
    const msg = res.data.msg;
    if (code === -1) {
      Message.error(msg);
    }
    return res;
  },
  async function(error) {
    // 如果发生了错误，判断是否是401
    console.dir(error);
    if (error.response.status === 401) {
      router.push("/login");
    }
    if (error.response.status === 500) {
      Message.error(error.response.data.msg);
    }
  }
);

async function request(method, uri, data, params) {
  let resp;
  uri = "/api" + uri;
  let commonError = { code: -1, message: "http访问失败！" };
  switch (method) {
    case "GET":
      try {
        resp = await instance.get(uri, { params: params });
      } catch (e) {
        return commonError;
      }
      break;
    case "POST":
      try {
        resp = await instance.post(uri, data, { params: params });
      } catch (e) {
        return commonError;
      }
      break;
    case "PUT":
      try {
        resp = await instance.put(uri, data, { params: params });
      } catch (e) {
        return commonError;
      }
      break;
    case "DELETE":
      try {
        resp = await instance.delete(uri, { params: params });
      } catch (e) {
        return commonError;
      }
      break;
    default:
      return commonError;
  }
  return resp.data;
}

export default request;
